@import '~antd/dist/antd.css';

li {
    list-style: none;
}

.ant-picker-calendar {
    background-color: #334065;
}

.ant-layout {
    align-items: center;
    background-color: transparent;
}

.ant-spin-dot {
    height: 50px;
    width: 50px;
}

.ant-spin-dot-item {
    height: 20px;
    width: 20px;
}

.ant-picker-calendar-date-content {
    height: 100%;
    transform: translateY(-100%);
}

.ant-picker-calendar-date-content-custom {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: #8bc34a6b;
    border-radius: 2px;
}